#page-content .center {
    width: 750px !important; }

.list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row; }

.list a {
    display: flex;
    flex: 1 0 100%;
    margin-bottom: 10px;
    align-items: center;
    justify-content: left;
    text-decoration: none;
    background-color: #F8F8FB;
    border: 1px solid #E5E5E5;
    border-radius: 7px;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px; }

.list a:hover {
    background-color: #F3F3F8;
    color: #333;
    border: 1px solid #888; }

.list a div {
    flex-basis: 20%;
    min-width: 140px; }

.list a div img {
    float: left;
    margin: 7px; }

@media screen and (max-width: 600px) {
    .list a div {
        min-width: 114px; }

    .list a div img {
        width: 100px; }

    .list a span {
        padding-left: 5px; } }
